<template>
  <div class="hello">

    <b-jumbotron header="0"
                 header-tag="h4"
                 header-level="4"
                 lead=""
                 text-variant=""
                 :fluid="true"
                 :container-fluid="true">
    </b-jumbotron>
    <b-card-group columns
                  class="content">
      <my-user v-for="(item, index) in list"
               :key="index"
               :item="item"></my-user>

    </b-card-group>

    <div>

    </div>
  </div>
</template>

<script>
import User from '../components/User'
import DataApi from '../assets/data/DataApi.js'

export default {
  name: 'index',
  components: {
    'my-user': User
  },
  data() {
    return {
      list: []
    }
  },
  methods: {
    getIndexUsers() {
      DataApi.getIndexUsers().then(result => {
        this.list = result
      })
    }
  },
  created() {
    this.getIndexUsers()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style type="scss" scoped>
.content {
  width: 90%;
  margin: 0 auto;
}
</style>



