<template>
  <div class="form">
    <b-form @submit="onSubmit"
            @reset="onReset"
            v-if="show">

      <b-form-group id="inputNameGroup"
                    label="姓名:"
                    label-for="inputName">
        <b-form-input id="inputName"
                      type="text"
                      v-model="form.name"
                      required
                      placeholder="请填写姓名">
        </b-form-input>
      </b-form-group>
      <b-form-group id="inputAmountGroup"
                    label="金额:"
                    label-for="inputAmount">
        <b-form-input id="inputAmount"
                      type="text"
                      v-model="form.amount"
                      required
                      placeholder="请填写投资的金额">
        </b-form-input>
      </b-form-group>
      <b-form-group id="inputIDGroup"
                    label="金额:"
                    label-for="inputID">
        <b-form-input id="inputID"
                      type="text"
                      v-model="form.ID"
                      required
                      placeholder="身份证号码">
        </b-form-input>
      </b-form-group>
      <b-form-group id="inputProvinceGroup"
                    label="省份:"
                    label-for="inputProvince">
        <b-form-select id="inputProvince"
                       v-model="form.province"
                       :options="provinces"
                       required
                       class="mb-3">
          <template slot="first">
            <option :value="undefined"
                    disabled>-- 请选择省份 --</option>
          </template>
        </b-form-select>

      </b-form-group>
      <b-button type="submit"
                variant="primary">Submit</b-button>
      <b-button type="reset"
                variant="danger">Reset</b-button>
    </b-form>
  </div>
</template>
<script>
import Province from '../assets/data/Provinces.js'

export default {
  data() {
    return {
      provinces: Province,
      selected: 0,
      show: true,
      form: {}
    }
  },
  methods: {
    onReset() {
      this.form = {}
    },
    onSubmit() {}
  }
}
</script>
<style>
.form {
  width: 90%;
  margin: 0 auto;
  padding: 50px 0;
  max-width: 500px;
}
</style>



