import axios from 'axios'

class DataApi {
  constructor() {}

  static getIndexUsers() {
    let url = 'http://42.159.252.51/json/users.json'

    return axios.get(url).then(result => {
      console.log(result)

      let { data } = result

      return data
    })
  }
}

export default DataApi



