import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/Index'
import Detail from '@/pages/Detail'
import UserForm from '@/pages/UserForm'
Vue.use(Router)

export default new Router({
  base: '/txswqhtml',
  routes: [
    {
      path: '/index',
      name: 'HelloWorld',
      component: Index
    },
    {
      path: '/detail',
      name: 'Detail',
      component: Detail
    },
    {
      path: '/userinfo',
      name: 'userinfo',
      component: UserForm
    }
  ]
})



