<template>
  <b-card :title="item.province">
    <div class="card-text">姓名：{{item.name}}</div>
    <div class="card-text">金额：{{item.amount}}</div>
    <small class="text-muted">更新时间：{{item.date}}</small>
  </b-card>
</template>
<script>
export default {
  props: ['item'],

  data() {
    return {}
  }
}
</script>
<style>
.text-muted {
  text-align: right;
}
</style>





